@import url('https://fonts.googleapis.com/css?family=Cairo:200,300,400,600,700,900&display=swap&subset=arabic,latin-ext');
$fontColor:#1078AD;
$listColor:#1A519E;

* {
    margin: 0;
    padding: 0;
    font-family: 'Cairo', sans-serif;
}

.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: url('../../Design/Assets/SVG/Merged\ bar-57.svg')no-repeat;
    background-size: cover;
    padding: .5em;
    position: fixed;
    top: 0;
    width: 100%;

    &__logo {
        padding-left: 1.5em;
    }

    &__listes {
        padding-right: 1.5em;

        &--link {
            color: black;
            padding: 1.5em;
            font-size: 2em;
            font-weight: bold;
            text-decoration: none;

            &:hover {
                text-decoration: none;
                color: $fontColor;
            }

            &:focus {
                color: $fontColor;
            }
        }

        .current {
            color: $fontColor;
        }


    }
}

.langue {
    color: black;
    text-decoration: none;
    padding: .1em .3em .1em .3em;
    font-size: 1.5em;
    text-decoration: none;
    height: 2em;
    align-items: center;
    background: transparent;
    border: 1px solid $fontColor;
    border-radius: .6em;

    &__icolangue {
        width: 1.3em;
        position: relative;
        top: .5em;
    }
}

